*{
  margin: 0;padding: 0;box-sizing: border-box;
}


.app {
  font-family: 'Arial', sans-serif;
 
  background: #f4f4f9;
  color: #333;
}
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  height: 10vh;
  background-color: #264653;
  color: white;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav-links a {
  text-decoration: none;
  color: white;
  transition: color 0.3s;
}

.nav-links a:hover {
  color: #2a9d8f;
}
.home {
  text-align: center;
  padding: 2rem;
  width: 100%;
  margin-top: 5vh;
}

.home h1 {
  font-size: 2.5rem;
  color: #264653;
}

.home p {
  font-size: 1.2rem;
  color: #6d6875;
}





/* Slider Container */
.slider {
  position: relative;
  width: 100%;
  height: 500px;
  margin: auto;
  overflow: hidden;
  border-radius: 10px;
  margin-top: -7vh;
  margin-bottom: 7vh;
}

/* Slide Styling */
.slide {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

.slide.active {
  opacity: 1;
}


.heading{
  height: 20vh;
  background-color: #264653;
  display: flex;
  align-items: center;
  margin-bottom: 5vh;
  border-radius: 10px;
}
.heading h1{
  color: #f4f4f9;
  font-size: 2.5rem;
}

/* Image Styling */
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Banner Text */
.banner-text {
  position: absolute;
  bottom: 50px;
  left: 50px;
  color: white;
  background: rgba(0, 0, 0, 0.5);
  padding: 20px;
  border-radius: 8px;
}

.banner-text h2 {
  font-size: 2rem;
  margin: 0;
}

.banner-button {
  display: inline-block;
  margin-top: 10px;
  padding: 10px 20px;
  background-color: #2a9d8f;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 0.3s;
}

.banner-button:hover {
  background-color: #264653;
}

/* Navigation Buttons */
.prev,
.next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  font-size: 2rem;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 50%;
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}

.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}
.details {
  text-align: center;
  padding: 2rem 1rem;
}

.details h2 {
  font-size: 2rem;
  color: #2a9d8f;
}

.details p {
  font-size: 1.2rem;
  color: #6d6875;
}

/* Cards Section */
.cards {
  display: flex;
  justify-content: space-around;
  gap: 1.5rem;
  margin: 2rem 0;
}

.card {
  background: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  border-radius: 8px;
  flex: 1;
  max-width: 300px;
  text-align: center;
  transition: transform 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
}

.card h3 {
  color: #264653;
  margin-bottom: 1rem;
}

.card p {
  color: #6d6875;
  font-size: 10px;
}
.card img{
height:200px;width: 250px;border-radius: 20px;
}

/* Footer Section */
.footer {
  width: 100%;
  text-align: center;
  background: #264653;
  color: white;
  height: 8vh;
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2%;
}
 

.footer p {
  margin: 0.5rem 0;
}

.footer a {
  color: #2a9d8f;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}
.abt-txt{
  height:80vh;
  width: 58%;
  /* background-color: red; */
  padding: 2vh 2%;
}
.abt-img{
  height:80vh;
  width: 40%;
  /* background-color: green; */
  background-image: url('https://img.freepik.com/free-vector/engineering-construction_23-2148880702.jpg?ga=GA1.1.1876014344.1717061920&semt=ais_hybrid');
  background-position: center;
  background-size: cover;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.127);
}

.about {
  display: flex;
  justify-content: space-around;
  border: 1px solid transparent;
  margin-top: 5vh;
  padding: 2vh 2%;
  
}
.about p{
  text-align: justify;
  word-spacing: 2px;
  margin-top: 20px;
}
.contact-container {
  padding: 2rem;
  
  background: #f7f9fc;

  text-align: center;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 1rem auto;
}

.contact-container h2 {
  font-size: 2rem;
  color: #264653;
  margin-bottom: 1rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.form-group label {
  font-weight: bold;
  color: #333;
  margin-bottom: 0.5rem;
}

.form-group input,
.form-group textarea {
  padding: 0.8rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
  outline: none;
  transition: border 0.3s;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: #264653;
}

.submit-button {
  padding: 0.8rem 1.5rem;
  background-color: #264653;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.submit-button:hover {
 color: #2a9d8f;
}
.project{
  height: 90vh;
  /* background-color: yellow; */
